* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  color: #ffffff;
  overflow-x: hidden;
  min-height: 100vh;
}
.dashboard-container {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  flex-wrap: wrap;
  gap: 1rem;
  min-height: 80px;
}
@media (max-width: 768px) {
  .header {
    justify-content: center;
    text-align: center;
    padding: 1rem 0;
    gap: 1.5rem;
  }
  .logo {
    order: 1;
    margin-left: 0;
    font-size: 1.5rem;
    width: 100%;
  }
  .platform-switcher {
    order: 2;
    justify-content: center;
  }
  .header-right {
    order: 3;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .logo {
    font-size: 1.25rem;
  }
  .platform-btn {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
}
.logo {
  font-size: 1.75rem;
  font-weight: 700;
  background: linear-gradient(45deg, #ff4458, #e1306c, #833ab4, #fd1d1d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-left: 2rem;
  flex-shrink: 0;
  text-shadow: 0 0 8px rgba(255, 68, 88, 0.3);
  filter: drop-shadow(0 0 4px rgba(255, 7, 127, 0.4));
  order: 2;
}
.platform-switcher {
  display: flex;
  gap: 1rem;
  align-items: center;
  order: 1;
  flex-shrink: 0;
}
.platform-btn {
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  font-size: 1rem;
}
.platform-btn.active {
  background: linear-gradient(45deg, #ff4458, #e1306c);
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(255, 68, 88, 0.3);
}
.platform-btn.youtube.active {
  background: linear-gradient(45deg, #ff0000, #ff4444);
  box-shadow: 0 10px 30px rgba(255, 0, 0, 0.3);
}
.platform-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}
.header-right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  order: 3;
  flex-shrink: 0;
}
.menu-container {
  position: relative;
}
.menu-btn {
  width: 45px;
  height: 45px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.2rem;
  color: white;
  position: relative;
}
.menu-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(255, 68, 88, 0.2);
}
.menu-btn.active {
  background: linear-gradient(45deg, #ff4458, #e1306c);
  box-shadow: 0 10px 30px rgba(255, 68, 88, 0.3);
}
.menu-icon {
  display: inline-block;
  transition: transform 0.3s ease;
}
.menu-btn.active .menu-icon {
  transform: rotate(90deg);
}
.dropdown-menu {
  position: absolute;
  top: calc(100% + 10px);
  right: 0;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 0.5rem 0;
  min-width: 200px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.3s ease;
  z-index: 1000;
}
.dropdown-menu.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.menu-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1.25rem;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  font-weight: 500;
}
.menu-item:hover {
  background: rgba(255, 255, 255, 0.1);
  padding-left: 1.5rem;
}
.menu-item-icon {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(45deg, #ff4458, #e1306c);
  border-radius: 6px;
  font-size: 0.8rem;
  flex-shrink: 0;
}
.user-profile {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}
.user-profile:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}
.avatar {
  width: 35px;
  height: 35px;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 0.9rem;
}
.nav-button {
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  color: white;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  text-align: center;
  justify-content: center;
}
.nav-button:hover {
  background: linear-gradient(45deg, #ff4458, #e1306c);
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(255, 68, 88, 0.3);
}
.action-button {
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  font-size: 0.9rem;
}
.action-button:hover {
  background: linear-gradient(45deg, #833ab4, #e1306c);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(131, 58, 180, 0.3);
}
.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}
.metric-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
}
.metric-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #ff4458, #e1306c, #833ab4);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.metric-card.youtube::before {
  background: linear-gradient(90deg, #ff0000, #ff4444, #cc0000);
}
.metric-card:hover {
  transform: translateY(-5px);
  background: rgba(255, 255, 255, 0.1);
}
.metric-card:hover::before {
  opacity: 1;
}
.metric-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.metric-title {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.metric-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  background: linear-gradient(45deg, #ff4458, #e1306c);
}
.metric-icon.youtube {
  background: linear-gradient(45deg, #ff0000, #ff4444);
}
.metric-value {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  background: linear-gradient(45deg, #ffffff, #ff4458);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.metric-value.youtube {
  background: linear-gradient(45deg, #ffffff, #ff0000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.metric-change {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
}
.change-positive {
  color: #00ff88;
}
.change-negative {
  color: #ff4757;
}
.chart-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 2rem;
  position: relative;
  overflow: hidden;
}
.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.chart-title {
  font-size: 1.5rem;
  font-weight: 600;
}
.chart-controls {
  display: flex;
  gap: 1rem;
}
.control-btn {
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}
.control-btn.active {
  background: linear-gradient(45deg, #ff4458, #e1306c);
}
.control-btn.youtube.active {
  background: linear-gradient(45deg, #ff0000, #ff4444);
}
.control-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}
.chart-placeholder {
  height: 300px;
  background: linear-gradient(135deg, rgba(255, 68, 88, 0.1), rgba(225, 48, 108, 0.1));
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.chart-placeholder.youtube {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.1), rgba(255, 68, 68, 0.1));
}
.chart-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #ff4458, #e1306c, transparent);
  animation: pulse 3s ease-in-out infinite;
}
.chart-line.youtube {
  background: linear-gradient(90deg, transparent, #ff0000, #ff4444, transparent);
}
@keyframes pulse {
  0%, 100% {
    opacity: 0.3;
    transform: translateX(-100%);
  }
  50% {
    opacity: 1;
    transform: translateX(100%);
  }
}
.activity-feed {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.activity-feed-compact {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 15px;
  padding: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  height: fit-content;
}
.activity-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}
.activity-item:hover {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding-left: 1rem;
  padding-right: 1rem;
}
.activity-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: linear-gradient(45deg, #ff4458, #e1306c);
  flex-shrink: 0;
  animation: glow 2s ease-in-out infinite alternate;
}
.activity-dot.youtube {
  background: linear-gradient(45deg, #ff0000, #ff4444);
}
@keyframes glow {
  from {
    box-shadow: 0 0 5px rgba(255, 68, 88, 0.5);
  }
  to {
    box-shadow: 0 0 20px rgba(255, 68, 88, 0.8);
  }
}
.activity-content {
  flex: 1;
}
.activity-title {
  font-weight: 600;
  margin-bottom: 0.25rem;
}
.activity-time {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
}
.auto-post-status {
  background: rgba(0, 255, 136, 0.1);
  border: 1px solid rgba(0, 255, 136, 0.3);
  border-radius: 10px;
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  margin-left: auto;
}
.auto-post-status.inactive {
  background: rgba(255, 71, 87, 0.1);
  border-color: rgba(255, 71, 87, 0.3);
}
.floating-particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}
.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(255, 68, 88, 0.3);
  border-radius: 50%;
  animation: float 20s infinite linear;
}
.particle.youtube {
  background: rgba(255, 0, 0, 0.3);
}
@keyframes float {
  0% {
    transform: translateY(100vh) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100px) rotate(360deg);
    opacity: 0;
  }
}
.grid-layout {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}
.grid-layout + .grid-layout {
  margin-top: -0.5rem;
}
@media (max-width: 768px) {
  .grid-layout {
    grid-template-columns: 1fr;
  }
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  .dashboard-container {
    padding: 1rem;
  }
  .platform-switcher {
    flex-direction: column;
    gap: 0.5rem;
  }
  .header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  .logo {
    position: static;
    transform: none;
    margin-bottom: 1rem;
  }
  .header-right {
    justify-content: center;
  }
}
.status-indicator {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #00ff88;
  animation: pulse-dot 2s infinite;
}
@keyframes pulse-dot {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.platform-data {
  display: none;
  animation: fadeIn 0.5s ease-in-out;
}
.platform-data.active {
  display: block;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}
.menu-overlay.show {
  opacity: 1;
  visibility: visible;
}
@keyframes slideIn {
  from {
    transform: translateX(100%) scale(0.8);
    opacity: 0;
  }
  to {
    transform: translateX(0) scale(1);
    opacity: 1;
