.chart-card {
  background: #121212;
  padding: 2rem;
  border-radius: 12px;
  margin-bottom: 2rem;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}
.chart-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 40px rgba(255, 68, 88, 0.2);
  border-color: rgba(255, 68, 88, 0.3);
}
.activity-feed {
  background: #121212;
  padding: 1.5rem;
  border-radius: 12px;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}
.activity-feed:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 40px rgba(255, 68, 88, 0.15);
  border-color: rgba(255, 68, 88, 0.2);
}
.activity-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
  gap: 1rem;
  padding: 1rem;
  border-radius: 10px;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.05);
}
.activity-item:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.1);
  transform: translateX(5px);
}
.thumb {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}
.thumb:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(255, 68, 88, 0.3);
  border-color: rgba(255, 68, 88, 0.5);
}
.chart-container {
  background: #121212;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}
.chart-container:hover {
  border-color: rgba(255, 68, 88, 0.3);
}
@keyframes activitySlideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.activity-item {
  animation: activitySlideIn 0.3s ease;
}
.chart-title {
  background: linear-gradient(45deg, #ffffff, #ff4458, #e1306c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.activity-item[data-platform="instagram"] .activity-dot {
  background: linear-gradient(45deg, #ff4458, #e1306c);
  box-shadow: 0 0 10px rgba(255, 68, 88, 0.5);
}
.activity-item[data-platform="youtube"] .activity-dot {
  background: linear-gradient(45deg, #ff0000, #ff4444);
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
}
@keyframes pulse {
  0%, 100% {
    opacity: 0.3;
    transform: translateX(-100%);
  }
  50% {
    opacity: 1;
    transform: translateX(100%);
  }
}
.chart-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #ff4458, #e1306c, transparent);
  animation: pulse 3s ease-in-out infinite;
}
.chart-line.youtube {
  background: linear-gradient(90deg, transparent, #ff0000, #ff4444, transparent);
}
.chart-placeholder {
  height: 300px;
  background: linear-gradient(135deg, rgba(255, 68, 88, 0.1), rgba(225, 48, 108, 0.1));
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.chart-placeholder.youtube {
  background: linear-gradient(135deg, rgba(255, 0, 0, 0.1), rgba(255, 68, 68, 0.1));
}
.chart-descriptions {
  margin-top: 4rem;
  padding: 3rem 2rem;
  background: linear-gradient(135deg, rgba(17, 24, 39, 0.95) 0%, rgba(31, 41, 55, 0.9) 100%);
  border-radius: 20px;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}
.description-container {
  max-width: 1200px;
  margin: 0 auto;
}
.description-title {
  font-size: 2rem;
  font-weight: 700;
  background: linear-gradient(45deg, #ffffff, #e5e7eb);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  margin-bottom: 2.5rem;
  letter-spacing: -0.025em;
}
.description-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}
.description-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.08);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}
.description-card:hover {
  transform: translateY(-4px);
  border-color: rgba(255, 255, 255, 0.15);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
}
.description-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  display: inline-block;
}
.description-card h4 {
  font-size: 1.25rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 1rem;
  letter-spacing: -0.025em;
}
.description-card p {
  color: #d1d5db;
  line-height: 1.6;
  font-size: 0.95rem;
  opacity: 0.9;
}
.description-legend {
  display: flex;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.legend-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 0.9rem;
  color: #e5e7eb;
  font-weight: 500;
}
.legend-color {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  display: inline-block;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
.legend-color.pink {
  background: linear-gradient(45deg, hotpink, #ff6b9d);
  box-shadow: 0 2px 12px rgba(255, 107, 157, 0.4);
}
.legend-color.red {
  background: linear-gradient(45deg, crimson, #ff4757);
  box-shadow: 0 2px 12px rgba(255, 71, 87, 0.4);
}
.legend-glow {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.3) 50%, transparent 100%);
  animation: glow-pulse 2s infinite;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}
@keyframes glow-pulse {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}
@media (max-width: 768px) {
  .chart-descriptions {
    margin-top: 2rem;
    padding: 2rem 1rem;
  }
  .description-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .description-legend {
    gap: 1.5rem;
  }
  .description-title {
    font-size: 1.5rem;
  }
}
.charts-row {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}
.hearts-chart-container {
  position: relative;
  height: 120px;
  margin-top: 20px;
  background-color: #121212;
  border-radius: 6px;
  overflow: hidden;
}
.chart-placeholder-hearts {
  height: 120px;
  background: transparent;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.chart-heart {
  position: absolute;
  top: 30%;
  left: 50%;
  font-size: 3rem;
  animation: heartbeat 1.2s ease-in-out infinite;
  z-index: 10;
}
.chart-heart.left {
  transform: translate(-150%, -50%);
  color: #ff1744;
  animation-delay: 0s;
}
.chart-heart.right {
  transform: translate(50%, -50%);
  color: #ff0033;
  animation-delay: 0.6s;
}
@keyframes heartbeat {
  0% {
    scale: 1;
    opacity: 0.8;
  }
  30% {
    scale: 1.3;
    opacity: 1;
  }
  60% {
    scale: 1.1;
    opacity: 0.9;
  }
  100% {
    scale: 1;
    opacity: 0.8;
  }
}
@media (max-width: 768px) {
  .charts-row {
    grid-template-columns: 1fr;
  }
  .chart-heart {
    font-size: 2rem;
  }
  .chart-heart.left {
    left: 40px;
  }
  .chart-heart.right {
    right: 40px;
  }
}
